import React, { Component } from 'react';
// 
import OverlayLoader from 'react-loading-indicator-overlay/lib/OverlayLoader';
import * as Call from './../Call/Call';
import ListForm from './../ListForm/ListForm';
import './List.css';

class List extends Component {
  constructor(props) {
    super(props);

    this.state = {
      fetching: false, //   false
      results: [], //     
      searchkey: '', //    
      show: false
    };
    this.handleChange = this.handleChange.bind(this);
  }

  //    
  Coins = async coin => {
    this.setState({
      fetching: true, // requesting..
      show: true
    });

    //  API 
    const post = await Call.GetAll();

    this.setState({
      results: post.data.result, //  JSON    state 
      fetching: false, // finish
      show: false
    });
  };

  //       
  componentDidMount() {
    this.Coins();
  }

  //    state 
  handleChange(e) {
    this.setState({
      searchkey: e.target.value
    });
  }

  render() {
    const lists = data => {
      data.sort();

      data = data.filter(info => {
        return (
          info.symbol
            .toLowerCase() // 
            .indexOf(this.state.searchkey.toLowerCase()) > -1 //  
        );
      });

      //   
      return data.map((info, i) => {
        return <ListForm symbol={info.symbol} name={info.name} key={i} />;
      });
    };

    return (
      <div>
        <div className="search-container">
          <input
            type="text"
            placeholder="Input Your Coin Initial"
            value={this.state.searchkey}
            onChange={this.handleChange}
          />
        </div>
        <OverlayLoader
          color={'red'} // default is white
          loader="ScaleLoader" // check below for more loaders
          text="Loading... Please wait!"
          active={this.state.show}
          backgroundColor={'white'} // default is black
          opacity="1" // default is .9
        >
          <div className="all-coin-list">{lists(this.state.results)}</div>
        </OverlayLoader>
      </div>
    );
  }
}

export default List;



