import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
// 
import OverlayLoader from 'react-loading-indicator-overlay/lib/OverlayLoader';
import * as Call from './../Call/Call';
import './Select.css';
// Form
import SelectForm from './../SelectForm/SelectForm';

class Select extends Component {
  constructor(props) {
    super(props);

    // initializes component state
    this.state = {
      fetching: false, //   false
      results: [], //     
      markets: [], //      
      show: false
    };
  }

  //     
  Coins = async coin => {
    this.setState({
      fetching: true, // requesting..
      show: true
    });

    //   
    const post = await Call.GetCoin(coin);
    //     
    this.setState({
      results: post.data.result.markets.base,
      fetching: false,
      show: false
    });

    //        
    let markets = []; // API   
    let num = 0; //       
    for (let index = 0; index < this.state.results.length; index++) {
      //   
      if (markets[num - 1] !== this.state.results[index].exchange) {
        //  
        markets[num] = this.state.results[index].exchange;
        //    
        num++;
      }
    }
    //    
    markets.sort();

    //  state  
    this.setState({
      markets: markets
    });
  };

  //       
  componentDidMount() {
    //        
    if (
      this.props.match.params.CoinName !== 'krw' && //  
      this.props.match.params.CoinName !== 'usd' && //  
      this.props.match.params.CoinName !== 'cny' && //  
      this.props.match.params.CoinName !== 'cad' && //  
      this.props.match.params.CoinName !== 'aud' && //  
      this.props.match.params.CoinName !== 'hkd' && //  
      this.props.match.params.CoinName !== 'inr' && //  
      this.props.match.params.CoinName !== 'jpy' && //  
      this.props.match.params.CoinName !== 'mxn' && //  
      this.props.match.params.CoinName !== 'sgd' && //  
      this.props.match.params.CoinName !== 'zar' //  
    ) {
      this.Coins(this.props.match.params.CoinName);
    }
  }

  render() {
    const markets = data => {
      return data.map((market, i) => {
        return (
          <SelectForm
            markets={market}
            initial={this.props.match.params.CoinName}
            key={i}
          />
        );
      });
    };

    return (
      <div className="selected">
        <NavLink to="/">
          <button className="selected-homelink-button">Go Back To Main</button>
        </NavLink>
        <div className="selected-symbol">
          {this.props.match.params.CoinName}
        </div>
        <div className="selected-guidance">
          <p>Tradable exchange</p>
        </div>
        <OverlayLoader
          color={'red'} // default is white
          loader="ScaleLoader" // check below for more loaders
          text="Loading... Please wait!"
          active={this.state.show}
          backgroundColor={'white'} // default is black
          opacity="1" // default is .9
        >
          <div className="selected-market">{markets(this.state.markets)}</div>
        </OverlayLoader>
      </div>
    );
  }
}

export default Select;



